import { Menu, MenuItem } from 'element-ui'

export default {
    components: {
        'el-menu': Menu,
        'el-menu-item': MenuItem,
    },
    data() {
        return {
            defaultSelected: 'current'
        }
    },
    methods: {
        select: function(key) {
            this.$emit('selected', key);
        }
    }
}



