import { Menu, MenuItemGroup, MenuItem, Submenu } from 'element-ui'

export default {
    components: {
        'el-menu': Menu,
        'el-menu-item': MenuItem,
        'el-menu-item-group': MenuItemGroup,
        'el-submenu': Submenu
    },
    props: ['showMenus'],
    data() {
        return {
            show: 'current',
        }
    },
    watch: {
        showMenus() {
            this.show = this.showMenus;
        }
    }
}



